@import "settings/color";
@import "settings/font";
@import "tools/mixin";
@import "tools/media";

.video-card {
  width: 310px;
  height: 200px;
  box-sizing: border-box;
  position: relative;
  border-radius: 10px;
  border: 1px solid #000;
  cursor: pointer; }

.video-card__video {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  img {
    width: 100%;
    height: 100%;
    border-radius: 10px; } }

.video-card__name {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 36px;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  padding: 5px 10px;
  border: 1px solid #000;
  border-radius: 9px;
  background-color: $yellow; }

.video-card__img {
  width: 25px;
  height: 25px;
  margin-right: 10px; }

.video-card__name-text {
  @include fontWSCL(normal, 18px, rgba(0, 0, 0, 0.8), 21px); }

.video-card__micro {}
